.site-title {
  font-family: $font__tt;
  font-size: 2.7rem;
  font-weight: 400;
}

.site-description{
  font-size: 1.1rem;
}

.site-content-contain {
  background-color: transparent;
}

.twentyseventeen-front-page article:not(.has-post-thumbnail):not(:first-child) {
  border-top: 0px solid #ddd;
}

.panel-content .drknss-wrap {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 3.5em auto 0;
  margin-left: 10%;
}

.panel-content .drknss-wrap:first-of-type {
  margin-top: 0px;
}

$colors: $drknss__tangelo rgba(25, 29, 31, 0.75) $drknss__mulberryish
  $drknss__cornflower-blue-dark $drknss__cerulean;
@for $i from 1 through 20 {
  #panel#{$i} {
    .panel-image {
      background-blend-mode: multiply;
      $key: random(length($colors));
      $nth: nth($colors, $key);
      $random: $nth !default;
      background-color: $random;
    }
  }
}

body.page-two-column:not(.archive) #primary .entry-content {
  float: none;
  width: 100%;
}

.panel-content {
  background: linear-gradient(
    to top,
    rgba(115, 124, 135, 0.73) 0%,
    rgba(9, 9, 121, 0) 75%,
    rgba(0, 0, 0, 0.3) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00000000", endColorstr="#4d000000", GradientType=0);
  bottom: 0;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 2;
  text-align: center;
  padding: 0 0 0 150px;
  
  .medium-wrap {
    padding-bottom: 2.5em;
  }
}

.content-front-page-wrap{
    height: 100vh;
}

body.page-two-column:not(.archive) #primary .entry-header::before {
  content: "";
  background: $drknss__yellowish;
  position: absolute;
  height: 2px;
  width: 100%;
  opacity: 0.3;
}

body.page-two-column:not(.archive) #primary .entry-header:first-child::before {
  display: none;
}

body.page-two-column:not(.archive) #primary .last-panel-entry-header::before {
  display:none;
}

body.page-two-column:not(.archive) #primary .entry-header {
  position: absolute;
  z-index: 1;
  width: 100%;
  .saison-page-link {
    float: right; 
  }  
}

body.page-two-column:not(.archive) #primary .last-panel-entry-header {
  position: relative;
}

.darkness-page-link,
.page-link-first,
.page-link-second {
  position: absolute;
}

.darkness-page-link{
  top: 1em;
  bottom: 0;
  right: 5em;
  width: 85px;
  height: 50px;
  transition: all 0.3s ease; 
}

.saison-page-link {
  width: 100%;
  height: 50px;
  transition: all 0.3s ease;
}

.page-link-first,
.page-link-second {
  padding-top: 5px;
  width: 100%;
  height: 50px;
  transition: all 0.2s ease-in-out;
  color: #FFF;
  letter-spacing: 1px; 
  vertical-align: middle;
}

.page-link-second {
  opacity: 0;
}

.drknss-icon {
	display: inline-block;
  fill:  rgba(221,221,239, .4);
	height: 1.5em;
	position: relative; /* Align more nicely with capital letters */
	top: 0.095em;
	vertical-align: middle;
	width: 1.5em;
}

.darkness-page-link:hover{
  width: 100px;
  .saison-page-link {
    padding-bottom: 40px;
  }
  .page-link-first {
    transform: translateX(-20px);
    padding-top: 7px;
  }
  .page-link-second {
    padding-top: 5px;
    transform: translateX(20px);
    opacity: 1;
    h2 {
      color: $drknss__red;
    }
  }
  .page-link-second:after {
    content: "";
    background: $drknss__yellowish;
    position: absolute;
    height: 1px;
    width: 20%;
  }
  .drknss-icon{
    fill: $drknss__red;
  }
}

.last-panel-entry-header {
  height: 50px;
  position: absolute;
  background: rgba(115, 124, 135, 0.73);
}

// * content-front-page : Image + prefix
.entry-content {
  margin-left: auto;
  margin-top: auto;
  img {
    display: block;
    -webkit-box-shadow: 2px 8px 6px -6px black;
    -moz-box-shadow: 2px 8px 6px -6px black;
    box-shadow: 2px 8px 6px -6px black;
  }
}

.content-wrap {
  display: inline-block;
  width: auto;
}

.mv-prefix {
  display: block;
  text-align: right;
  color: $drknss__tangelo;
  letter-spacing: 0.4em;
  padding-right: 0.3em;
  padding-top: 0.7em;
}

