/*--------------------------------------------------------------
## Navigaton-Top (navigation)
--------------------------------------------------------------*/
.navigation-top {
  text-shadow: 0px 0px 6px rgba(162, 176, 168, 0.99);
  background: transparent;
  border: 0px;
  font-size: 1rem;
}

.navigation-top .wrap {
  max-width: 1000px;
  padding: 0;
}

.navigation-top a {
  color: $drknss__text-main;
  font-weight: 300;
  transition: color 0.2s;
}
.main-navigation a:hover {
  color: $drknss__gamboge;
}
.navigation-top .current-menu-item > a,
.navigation-top .current_page_item > a {
  color: $drknss__scarlet;
}
/*--------------------------------------------------------------
## Navigaton-Left (Darkness Menu)
--------------------------------------------------------------*/

.drknss-site-header {
  position: fixed;
  width: 150px;
  // left: 2.2em;
  bottom: 0;
  z-index: 7;
  font-weight: 300;
}
.menu-darkness-navigation-container {
  height: 97vh;
  padding: 1em 0.5em .5em 1em;
  margin: 1em 0.5em .5em 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.menu-bar {
  list-style: none;
  margin: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  overflow: visible;
  border-bottom: rgba(221,221,239, .4) solid 2px;
  border-top: rgba(221,221,239, .4) solid 2px;
  border-left: rgba(221,221,239, .4) solid 2px;
  border-radius: 25px 1px 0px 55px/25px 1px 0px 55px;

  li {
    flex-grow: 0;
    width: 100%;
  }
}

.menu-bar li:first-of-type,
.menu-bar li:last-of-type {
  display: none;
}

.navigation-left a {
  padding: 1em 0.5em;
  position: relative;
  width: 100%;
  left: 25%;
  text-shadow: 0px 0px 1px rgba(239, 240, 221, 0.99);
  transition: color 0.2s;
}
a:focus {
  outline: none;
}
.drknss-blg-link > a {
  position: relative;
  padding: 1em 0.5em;
}

.navigation-left .current-menu-item > a {
  color: rgba(221, 221, 239, 0.4);
  position: relative;
  overflow-x: visible;
}

.navigation-left .current-menu-item > ._mPS2id-h .off-cursor {
  position: relative;
}

.drknss-blg-link a {
  color: $drknss__cornflower-blue-light;
  opacity: 0.8;
}

.on-cursor {
  visibility: hidden;
  display: none;
}

.navigation-left .current-menu-item > .mPS2id-highlight .on-cursor {
  color: $drknss__scarlet;
  visibility: visible;
  display: inline;
  position: absolute;
  min-width: 12em;
  text-shadow: 0px 0px 6px rgba(162, 176, 168, 0.99);
  opacity: 0.8;
  transition-property: all;
}

.navigation-left .current-menu-item > .mPS2id-highlight:hover .on-cursor,
.drknss-blg-link:hover .on-cursor {
  color: $drknss__scarlet;
}
.navigation-left .current-menu-item:hover .on-cursor,
.drknss-blg-link:hover .on-cursor {
  visibility: visible;
  color: $drknss__gamboge;
  display: inline;
  position: absolute;
  min-width: 12em;
  transition-property: all;
  text-align: left;
  overflow-x: visible;
  text-shadow: 0px 0px 9px rgba(244, 102, 91, 0.99);
  opacity: 0.8;
}
.drknss-blg-link:hover .on-cursor {
  color: $drknss__cornflower-blue-light;
  text-shadow: 0px 0px 3px rgba(239, 240, 221, 0.99);
  opacity: 1;
}
.navigation-left .current-menu-item:hover .off-cursor,
.drknss-blg-link:hover .off-cursor {
  visibility: hidden;
  display: none;
  position: relative;
}
.navigation-left .current-menu-item > .mPS2id-highlight .off-cursor {
  visibility: hidden;
  display: none;
  position: relative;
}

.admin-bar.twentyseventeen-front-page.has-header-image .custom-header-media,
.admin-bar.twentyseventeen-front-page.has-header-video .custom-header-media,
.admin-bar.home.blog.has-header-image .custom-header-media,
.admin-bar.home.blog.has-header-video .custom-header-media {
  height: calc(100vh - 32px);
}

.has-strong-magenta-background-color {
  background-color: #a156b4;
}
.has-black-color {
  background-color: #a156b4;
}
.has-very-light-gray-background-color {
  background-color: #eee;
}
.has-very-light-gray-color {
  color: #eee;
}

/*--------------------------------------------------------------
## Footer Navigation
--------------------------------------------------------------*/
// #menu-footer-navigation a {
// }

.widget-area .menu-social-links-menu-container ul li a,
.widget-area .menu-footer-navigation-container ul li a,
.widget .widget-title a {
  background-color: transparent;
  color: $drknss__sea-green-light;
  opacity: 0.8;
}

.widget-area .menu-social-links-menu-container ul li a:focus,
.widget-area .menu-footer-navigation-container ul li a:focus,
.widget-area .menu-social-links-menu-container ul li a:hover,
.widget-area .menu-footer-navigation-container ul li a:hover,
.site-footer .widget-area a:hover,
.site-footer .widget-area a:focus,
.drknss_custom_link_widget:hover,
.drknss_custom_link_widget:focus {
  color: $drknss__grayish-vermilion;
  box-shadow: none;
  h2 a {
    color: $drknss__grayish-vermilion;
  }
  h2::after {
    background: $drknss__grayish-vermilion;
  }
}

.site-footer .widget-area a {
  box-shadow: none;
}
/*--------------------------------------------------------------
## * Social Link Menu
--------------------------------------------------------------*/
.widget-area .menu-footer-navigation-container ul li {
  border: none;
  padding: 0 0 0.5em 0;
}
.widget-area .menu-social-links-menu-container ul li {
  border: none;
  padding: 0.3em 0 0.5em 0;
}

.widget_nav_menu:first-of-type {
  padding-bottom: 0px;
}

.widget-area .menu-footer-navigation-container ul li:first-of-type {
  padding-top: 1em;
}
.widget-area .menu-social-links-menu-container ul,
.widget-area .menu-footer-navigation-container ul {
  padding: 1em 0em 0 2em;
}
.widget-area .menu-social-links-menu-container ul li a,
.widget-area .menu-footer-navigation-container ul li a {
  font-size: 1.2em;
  height: 40px;
  margin: 0 1em 0.5em 0;
  text-align: center;
  width: 40px;
}

.widget-area .menu-footer-navigation-container ul li a {
  text-transform: uppercase;
  font-size: 1.3em;
}

.widget-area .menu-social-links-menu-container ul li a .icon {
  font-size: 1.2em;
  margin-right: 0.5em;
}

.widget_search {
  clear: both;
}

.widget-area .social-navigation {
  width: 100%;
}

.widget ul li a,
.widget ul li,
.site-footer .widget-area ul li a,
.site-footer .widget-area ul li a {
  box-shadow: none;
  border: none;
}

/*--------------------------------------------------------------
## * Custom Link Widget
--------------------------------------------------------------*/
.drknss_custom_link_widget {
  width: 18em;
  padding-left: 2em;
  padding-bottom: 2em;
  margin: 1em auto;
  border: 1px solid;
  border-image: linear-gradient(
      80deg,
      rgba(162, 176, 168, 0.99),
      rgba(221, 221, 239, 0.4)
    )
    1;
}

.drknss_custom_link_widget h2 {
  color: $drknss__cornflower-blue-light;
  text-align: center;
  font-weight: 300;
  padding-top: 0.9em;
  padding-right: 0.9em;
  font-size: 1.143em;
  margin-bottom: 1em;
}
.drknss_custom_link_widget h2:after {
  content: "";
  // position: absolute;
  background: $drknss__sea-green-light;
  width: 33%;
  height: 2px;
  display: block;
  margin: 1em auto 0;
}
.drknss_custom_link_widget:focus,
.drknss_custom_link_widget:hover {
  background: rgb(162, 176, 168);
  background: linear-gradient(
    to bottom,
    rgba(162, 176, 168, 0.4) 0%,
    rgba(239, 240, 221, 0.42) 100%
  );
}
.widget .widget-title a {
  box-shadow: none;
  font-family: $font__demi;
  font-weight: 500;
}
/*--------------------------------------------------------------
## * Info Link Menu
--------------------------------------------------------------*/
.site-info {
  float: none;
  margin: 0 auto;
  
		padding: 0 0 0 0;
		width: 59%;
}
.menu-info {
  list-style: none;
  margin: 0;
text-align: center;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-transform: uppercase;
  li::before {
    content: ' \2759';
    display: inline-block;
    
  }
  li:first-child:before,
.footer-nav .children li:before {
  content: "";
	display: none;
}
  a {
    color: $drknss__mulberryish;
    -webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
  }
  

}
