/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
  border-top: none;
  margin-top: 0px;
  padding-top: 42px;
  background: rgb(115, 124, 135);
  background: linear-gradient(
    to bottom,
    rgba(115, 124, 135, 0.73) 5%,
    rgba(45, 59, 69, 0.9) 45%,
    rgba(25, 29, 31, 1) 75%,
    rgba(0, 0, 0, 1) 100%
  );
  .wrap {
    padding-left: 1em;
    padding-right: 1em;
  }
}

// ! d√©placer la ligne en fin de front page
.site-footer::before {
  content: "";
  background: rgba(221,221,239, .4);
  position: absolute;
  left: 15%;
  height: 2px;
  width: 70%;
}

/*--------------------------------------------------------------
## Blockquote
--------------------------------------------------------------*/
.drknss-footer-quote {
  background: rgb(90, 112, 136);
  background: linear-gradient(
    90deg,
    rgba(100, 125, 134, 0.55) 0%,
    rgba(90, 112, 136, 0.55) 50%,
    rgba(100, 125, 134, 0.55) 100%
  );
  border-left: 2px solid $drknss__gamboge;
}

footer blockquote {
  color: #ddd;
  font-family: $font__quote;
  font-size: 23px;
  width: 60%;
  margin: 50px auto;
  padding: 1.2em 30px 1.2em 75px;
  position: relative;

  footer {
    font-size: 0.9em;
    font-family: $font__sans;
    font-weight: 600;
  }
}

footer blockquote footer cite {
  font-size: 0.8em;
  display: inline;
  font-weight: 300;
}

footer blockquote::before {
  content: "\201C";
  color: $drknss__gamboge;
  display: block;
  font-size: 4em;
  position: absolute;
  left: 10px;
  top: -15px;
}

footer blockquote p::after {
  content: "";
}

@media screen and (min-width: 48em) {
  .wrap {
    max-width: 1200px;
  }
}

.site-footer {
  .widget-area {
    display: flex;
    flex-direction: row;
  }
}

/*--------------------------------------------------------------
## Footer Widget
--------------------------------------------------------------*/
// * MENUS NAV
.site-footer .widget-column.footer-widget-1 {
  float: none;
  margin-right: 0.5em;
  width: 33.333%;
}

// * PDF List
.site-footer .widget-column.footer-widget-2 {
  float: none;
  clear: both;
  width: 33.333%;
  margin-right: 0.5em;
  margin-left: 0.5em;
}

// * LAST BOOK
.site-footer .widget-column.footer-widget-3 {
  float: none;
  width: 50%;
  margin-left: 0.5em;
  .widget_image {
    padding-left: 1em;
  }
  .widget a img {
    margin-top: 1em;
    max-width: 265px;
    box-shadow: none;
  }
}
